const entitiesDefinitions = require('./entities')

function GSS(agm, options) {  
  let searchProvider = null;
  let searchProviderReady = null;
  var allEntityTypes;

  const isGssEnabled = ((options.searchProviders && options.searchProviders.length > 0) || options.provider) 

  if (!isGssEnabled) {
    return {
      search() {
        console.log(`GSS isn't enabled.`)
      }, 
      addEntityType() {
        console.log(`GSS isn't enabled.`)
      }
    }
  }

  if (options.side === 'helper' && typeof gss !== "undefined") {
    const manager = gss.createDesktopManager(agm, {
      debug: false
    });
    manager.start();

    if (options.searchProviders && options.searchProviders.length > 0) {
      options.searchProviders.forEach(providerName => {
        manager.add({name: providerName, type: 'agm', application: providerName})
      })
    }

    var searchService = new gss.GlueSearchService(agm, {
      target: {
        application: options.target
      }
    })
    var searchReady = searchService.ready()

    searchReady.then(api => {
      api.onEntityTypes((err, entityTypes) => {
        if (err) {
          throw new Error(err)
        }
        allEntityTypes = entityTypes
      })
    })
  }
  
  function addEntityType (name, handler) {
    return new Promise((resolve, reject) => {
      if (typeof name !== 'string') {
        throw Error('A entity name is required to register a search provider');
      }

      if (entitiesDefinitions[name] === undefined) {
        throw Error(`Unsupported entity: "${name}"`);
      }

      if (typeof handler !== 'function') {
        throw Error('A search handler is required to register a search provider');
      }

      if (!searchProvider) {
        searchProvider = new gss.GlueSearchProvider(agm, { 
          debug: false, 
          measureLatency: false,
          target: {
            application: options.target
          }
        })

        searchProviderReady = searchProvider.start()
      }
      
      searchProviderReady
        .then(() => {
          const entity = gss.GssEntityType.fromJS(entitiesDefinitions[name]);
          searchProvider.addEntityType(entity, handler);
          resolve();
        })
        .catch(reject)
    });
  }

  function search (name, query) {
    return new Promise((resolve, reject) => {
      searchReady.then(api => {
        let response = [];
        const entityType = allEntityTypes && allEntityTypes.get(name);
        if (!entityType) {
          return reject('Search provider not found')
        }
        const currentQuery = searchService.createQuery(entityType);
        
        currentQuery
          .onData(result => {
            response = response.concat(result.entities)
            if (result.isLast) {
              resolve(response)
            }
          })
          .search(query)
      }).catch(reject)
    });
  }

  return {
    addEntityType: addEntityType,
    search: search
  }
}

module.exports = GSS;


