const Model = require('objectmodel');
const Account = require('../models/Account');
const Contact = require('../models/Contact');

var AccountContactList = Model({
  account: Account,
  contacts: Model.Array(Contact)
})

module.exports = function AccountController(agm, config) {
  return {
    create(args) {
      args = args || {};
      return new Promise((resolve, reject) => {
        let account = new Account(args.account);
        agm.invoke('T42.CRM.CreateAccount', {account: args.account}, 'all', {method_response_timeout: config.timeout}, resolve, reject);
      })
    },
    resolve(args) {
      args = args || {};
      return new Promise((resolve, reject) => {
        let account = new Account(args.account);
        agm.invoke('T42.CRM.ResolveAccount', {account: args.account}, 'all', {method_response_timeout: config.timeout}, resolve, reject);
      })
    },
    update(args) {
      args = args || {};
      return new Promise((resolve, reject) => {
        let account = new Account(args.account);
        const autoCreate = args.autoCreate
        agm.invoke('T42.CRM.UpdateAccount', {account: args.account, autoCreate: autoCreate}, 'all', {method_response_timeout: config.timeout}, resolve, reject);
      })
    },
    delete(args) {
      args = args || {};
      return new Promise((resolve, reject) => {
        let account = new Account(args.account);
        agm.invoke('T42.CRM.DeleteAccount', {account: args.account}, 'all', {method_response_timeout: config.timeout}, resolve, reject);
      })
    },
    get(args) {
      args = args || {};
      return new Promise((resolve, reject) => {
        let account = new Account(args.account);
        agm.invoke('T42.CRM.GetAccount', {account: args.account}, 'all', {method_response_timeout: config.timeout}, resolve, reject);
      })
    },
    sync(args) {
      args = args || {};
      return new Promise((resolve, reject) => {
        let account = new Account(args.account);
        agm.invoke('T42.CRM.SyncAccount', {account: args.account}, 'all', {method_response_timeout: config.timeout}, resolve, reject);
      })
    },
    resolveContactList(args) {
      args = args || {};
      return new Promise((resolve, reject) => {
        let contactList = new AccountContactList({ account: args.account, contacts: args.contacts });
        agm.invoke('T42.CRM.ResolveAccountContactList', {account: args.account, contacts: args.contacts }, 'all', {method_response_timeout: config.timeout}, resolve, reject);
      })
    }
  }
}


