const GlueCore = require('tick42-glue-core');
const defaultConfig = require('./config.js');
const CRMCreator = require('./crm.js');

module.exports = function Tick42CRM(options) {
  const crmConfig = {
    side: options.side || defaultConfig.side,
    timeout: options.timeout || defaultConfig.timeout,
    target: options.application || 'GssDesktopManager',
    searchProviders: options.searchProviders || [],
    provider: options.provider
  }
  
  const glueConfig = Object.assign({}, defaultConfig.glue, options);
  glueConfig.auth = glueConfig.authentication;

  return new Promise((resolve, reject) => {
    let gluePromise;
    if (options.glue) {
      gluePromise = Promise.resolve(options.glue);
    } else {
      gluePromise = GlueCore(glueConfig);
    }

    gluePromise
      .then(glue => {
        const CRM = CRMCreator(glue.agm, crmConfig);
        resolve({
          CRM, glue
        });
      }).catch(err => {
        reject(err);
      });
  });
};


