const Contact = require('../models/Contact');
const Id = require('../models/Contact');
const Model = require('objectmodel');

var Call = Model({
  contact: Contact,
  participants: [Model.Array(Contact)],
  subject: [String],
  dueDate: [String],
  description: [String]
});

module.exports = function ContactController(agm, config) {
  return {
    create(args) {
      args = args || {};
      return new Promise((resolve, reject) => {
        const contact = new Contact(args.contact);
        agm.invoke('T42.CRM.CreateContact', {contact: args.contact}, 'all', {method_response_timeout: config.timeout}, resolve, reject);
      });
    },
    resolve(args) {
      args = args || {};
      return new Promise((resolve, reject) => {
        const contact = new Contact(args.contact);
        agm.invoke('T42.CRM.ResolveContact', {contact: args.contact}, 'all', {method_response_timeout: config.timeout}, resolve, reject);
      });
    },
    update(args) {
      args = args || {};
      return new Promise((resolve, reject) => {
        const contact = new Contact(args.contact);
        const autoCreate = args.autoCreate;
        agm.invoke('T42.CRM.UpdateContact', {contact: args.contact, autoCreate: autoCreate}, 'all', {method_response_timeout: config.timeout}, resolve, reject);
      });
    },
    delete(args) {
      args = args || {};
      return new Promise((resolve, reject) => {
        const contact = new Contact(args.contact);
        agm.invoke('T42.CRM.DeleteContact', {contact: args.contact}, 'all', {method_response_timeout: config.timeout}, resolve, reject);
      });
    },
    get(args) {
      args = args || {};
      return new Promise((resolve, reject) => {
        const contact = new Contact(args.contact);
        agm.invoke('T42.CRM.GetContact', {contact: args.contact}, 'all', {method_response_timeout: config.timeout}, resolve, reject);
      })
    },
    sync(args) {
      args = args || {};
      return new Promise((resolve, reject) => {
        const contact = new Contact(args.contact);
        agm.invoke('T42.CRM.SyncContact', {contact: args.contact}, 'all', {method_response_timeout: config.timeout}, resolve, reject);
      });
    },
    logCall(args) {
      args = args || {};
      return new Promise((resolve, reject) => {
        const call = new Call(args);
        agm.invoke('T42.CRM.LogContactCall', call, 'all', {method_response_timeout: config.timeout}, resolve, reject)
      });
    }
  }
};


